@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
body {
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: 'Varela Round', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color:rgb(58, 59, 61) ;
}
html{
  height: 100%;
  width: 100%;
  margin: 0;
}
code {
  font-family: 'Varela Round', sans-serif;
}

/* Szary- 58, 59, 61
Czerwony- 240, 50, 50
Szary2-71, 67, 67
Biały-250, 245, 245 */

.nav{

    margin-left: 30px;
    margin-right: 30px;
    border-bottom: 2px solid rgb(240, 50, 50) ;
    border-left: 2px solid rgb(240, 50, 50) ;;
    border-right: 2px solid rgb(240, 50, 50) ;;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px ;

    display: flex;
    align-items: center ;
    justify-content: space-between ;
    }

    .navlinks{
        margin: 10px;
        display: flex;
        justify-content: space-between;
    }
    .logo{
        margin-left: 40px;
        padding: 10px;
        display: flex;
    }

@media screen and (max-width: 1000px) {
    .nav{
        margin:0;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    .navlinks {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    button{
        width: 100%;
    }
}
/* Szary- 58, 59, 61
Czerwony- 240, 50, 50
Szary2-71, 67, 67
Biały-250, 245, 245 */

.btn {
    font-family: 'Varela Round', sans-serif;
    font-weight:500;
    border:none;
    cursor: pointer;
    transition: 0.1s ease-out;
    text-transform: uppercase;
    text-decoration: none;
    outline: none;
}
button:active, button:focus {
    outline: 0;
    outline-style: none;
  }
/* btn col & styles */

.btn--primary--full {
    color: white;
    border: 2px solid rgb(240, 50, 50);
    border-radius: 8px;
    background-color:rgb(58, 59, 61) ;
    box-shadow:  0 0 2px red;
    transition: all 0.3s ease-out;
}
.btn--primary--full:hover{
 background-color: rgb(240, 50, 50);
}

/* btn size  */
.btn--medium{
    padding: 5px 15px;
    margin: 10px;
    font-size: 20px;
}

.Main{
    margin: 20px 30px ;
    display: grid;
    grid-template-columns:15% auto ;
    grid-template-rows:800px 160px;

}

.oferty{
grid-row-start: 1;
grid-row-end:3;
border-radius: 20px;
border:2px solid rgb(240, 50, 50) ;
}

.maincontent{
grid-column-start: 2;
grid-column-end: 2;
grid-row-start: 1;
grid-row-end:3;
}

@media screen and (max-width: 1000px) { 
    .Main{
        margin: 0px;
        padding: 0px;
        grid-template-rows: 800px 200px;
        margin-top: 10px;
    }
    .oferty{
        grid-column-start: 1;
        grid-column-end: 3 ;
        grid-row-start: 1;
        grid-row-end:2;
    }
    .maincontent{
        grid-column-start: 1;
        grid-column-end: 3 ;
        grid-row-start: 2;
        grid-row-end:2;
    }
}
