.App{
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: auto ;
    grid-template-rows: 8% 1000px 8% ;
}

@media screen and (max-width: 1000px){

    .App{
        grid-template-rows: auto 1000px 80px;
    }
}
